body {
  font-family: monospace;
  overflow-x: hidden;
}

.App {
  min-height: 100vh;
  max-width: 100vw;
  box-sizing: border-box;
}

#hero, #about, #contact, #projects {
  display: flex;
  min-height: 100vh;
  width: 100vw;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  box-sizing: border-box;

}

#name {
  font-size: 10rem;
  text-align: center;
}

#occupation{
  font-size: 3rem;
}

 #about-copy {
  width: 75%;
  line-height: 2.5;
  font-size: 1rem;
  text-indent: 4rem;
 }

 .project:hover{
  background-color: aliceblue;
 }


#contact-heading, #projects-heading{
  font-size: 4rem;
  text-align: center;
}
 .contact-link{
  font-size: 2rem;
 }

 .social-link {
  text-decoration: none;
  color: black;
 }

 .contact-link:hover,  .contact-link > a:hover{
  color: #555;
 }


 @media screen and (max-width: 1100px){
  #name {
    font-size: 7rem;
  }

 }

 @media screen and (max-width: 800px){
  #name {
    font-size: 5rem;
  }

  #occupation{
    font-size: 1.5rem;
  }
  
 }

 @media screen and (max-width: 600px){
  #about-copy {
    font-size: 1rem;
    width: 90%;
    line-height: 1.5;
  }
  #projects-container{
    margin-left: 10px;
  }

  .project {
    max-width: 90%;
  }

  #contact-heading, #projects-heading{
    font-size: 2.5rem;
    text-align: center;
  }

  .contact-link {
    font-size: 1rem;
  }
  
 }

 @media screen and (max-width: 374px){
  #name {
    font-size: 2rem;
  }
  #occupation {
    font-size: 1rem;
  }

  #projects-heading, #contact-heading {
    font-size: 2rem;
  }

  .contact-link {
    font-size: 10px;
  }
 }